<template>
  <div class="message-center-container">
      <router-link 
      :to="{name: 'notice', query: {
          type: item.type
      }}" class="message-item bg-white row" v-for="(item, index) in lists" :key="index">
          <img :src="item.img" width="38px" height="38px" />
          <div class="message-info ml10 column">
              <div class="title lg normal">{{item.title}}</div>
              <div class="prev-msg sm one-txt-cut">{{item.content}}</div>
          </div>
      </router-link>
  </div>
</template>

<script>
import {getMessageLists} from "@API/store"
export default {
    name: "messageCenter",
    data() {
        return {
            lists: []
        }
    },
    methods: {
        $getMessageLists() {
            getMessageLists().then(res => {
                if(res.code == 1) {
                    this.lists = res.data
                }
            })
        }
    },
    mounted() {
        this.$getMessageLists()
    }
}
</script>

